@tailwind base;
@tailwind components;
@tailwind utilities;

.scroll-container-v2 {
  width: 100%;
  max-width: 1100px;
  flex: 0 auto;
  justify-content: flex-start;
  margin-top: 0;
  margin-bottom: 0;
  padding-bottom: 0%;
  display: flex;
  overflow: visible;
}

.socialmedia-container-mobile {
  width: 100%;
  height: auto;
  grid-column-gap: 3%;
  grid-row-gap: 6%;
  flex-flow: wrap;
  grid-template-rows: auto auto auto auto auto auto;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-auto-columns: 1fr;
  align-content: stretch;
  justify-content: space-around;
  align-items: flex-start;
  display: flex;
  position: relative;
  overflow: visible;

  @media (max-width: 479px) {
    grid-column-gap: 10%;
    grid-row-gap: 10%;
    flex-wrap: nowrap;
    justify-content: flex-start;
    padding-bottom: 20px;
    overflow: auto;
  }
}

.down {
  /* width: 17%; */
  background-image: none;
  flex: none;
  justify-content: center;
  margin-top: 0%;
  margin-right: auto;
  padding-bottom: 0%;
  position: relative;
}
.up {
  /* width: 20%; */
  background-image: none;
  flex: none;
  margin-right: auto;
  padding-bottom: 0%;
  position: relative;
  top: auto;
}
.socialmedia-card-01 {
  width: 11vw;
  height: auto;
  aspect-ratio: 9 / 16;
  object-fit: cover;
  background-image: url(https://assets-global.website-files.com/619b0fa…/659efae…_slapcapbungee2.jpg);
  background-position: 50%;
  background-size: cover;
  border-radius: 20px;
  flex-direction: column;
  align-items: center;
  margin-right: 3%;
  padding-bottom: 5%;
  display: flex;

  
  

  ._01 {
    width: 20%;
    flex: none;
    justify-content: center;
    margin-right: auto;
  }
}

.videobg1 {
  z-index: 5;
  width: 100%;
  height: 100%;
  border-radius: 16px;
  position: relative;
}

.w-background-video {
  height: 359px;
  color: #fff;
  position: relative;
  overflow: hidden;
}

.w-background-video > video {
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -100;
  background-position: 50%;
  background-size: cover;
  margin: auto;
  position: absolute;
  top: -100%;
  bottom: -100%;
  left: -100%;
  right: -100%;
}

@layer base {
  body {
    @apply overflow-hidden text-primary font-primary bg-white;
  }
  .h1 {
    @apply text-[54px] lg:text-[108px] font-primary font-bold capitalize leading-[120%] tracking-[-0.05em] mb-2;
  }
  .menu-h1 {
    @apply text-[54px] lg:text-[50px] font-primary font-bold capitalize leading-[120%] tracking-[-0.05em] mb-2;
  }

  .section {
    @apply h-screen w-screen;
  }
  .btn {
    @apply py-[18px] px-[50px] h-[66px] flex items-center justify-center text-base uppercase font-secondary font-semibold bg-primary text-white;
  }
  .menu-btn {
    @apply py-[18px] px-[18px] h-[20px] flex items-center justify-center text-base uppercase font-secondary font-semibold bg-primary text-white;
  }
}
